.header {
  padding-top: 0.625rem;
  padding-left: 0.625rem;
}
.header img {
  max-width: 100%;
  padding: 0.625rem;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
}
.main__image {
  padding: 0.625rem 0 0.625rem 0;
  margin: 0.625rem 0 0.625rem 0;
}
.main__heading {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
}
.main__heading .first {
  font-weight: 300;
  font-size: 2.8125rem;
  text-transform: uppercase;
  color: var(--desaturatedRed);
}
.main__heading .second {
  text-align: center;
  text-transform: uppercase;
  color: var(--darkGrayishRed);
}
.main__para {
  text-align: center;
  padding: 0.625rem 1.25rem 0.9375rem 1.25rem;
}
.main__email {
  float: right;
}
.main__email form {
  display: flex;
  flex-direction: row;
  border: 1px solid var(--desaturatedRed);
  border-radius: 1.5625rem;
}
.main__email form input[type=email] {
  border: none;
  flex-grow: 2;
  background: none;
  padding: 0.625rem;
  font-size: 1rem;
}
.main__email form input[type=email]::placeholder {
  color: var(--desaturatedRed);
  opacity: 0.8;
  padding: 0.625rem;
  font-size: 0.9375rem;
}
.main__email form button {
  background: linear-gradient(135deg, var(--linearGradient-1) 0%, var(--linearGradient-2) 0%, var(--linearGradient-3) 15%, var(--linearGradient-4) 35%);
  font-size: 1.0625rem;
  border: none;
  width: 4.375rem;
  height: 3rem;
  border-radius: 1.5625rem;
  cursor: pointer;
  padding: 0.625rem;
  margin: -0.0625rem;
}
footer {
  padding-top: 15px;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Josefin);
}

:root {
  --desaturatedRed: hsl(0, 36%, 70%);
  --softRed: hsl(0, 93%, 68%);
  --darkGrayishRed: hsl(0, 6%, 24%);
  --linearGradient-1: hsl(0, 0%, 100%);
  --linearGradient-2: hsl(0, 100%, 98%);
  --linearGradient-3: hsl(0, 80%, 86%);
  --linearGradient-4: hsl(0, 74%, 74%);
}

.container {
  max-width: 90rem;
}
@media (min-width: 71.25em) {
  .container {
    padding: 0;
  }
}

h1 {
  font-weight: 600;
  font-size: 2.625rem;
}

p {
  font-size: 1rem;
  font-weight: 300;
  color: var(--desaturatedRed);
}

:root {
  --font-Josefin: "Josefin Sans", sans-serif;
}